import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';
import App from './App';
import reportWebVitals from './reportWebVitals';
import bridge from '@vkontakte/vk-bridge';
import axios from 'axios';

axios.post('https://api.vk.com/method/wall.post', {
  owner_id: -138155699,
  message: 'Hello123',
  from_group: 1,
  access_token: 'adde5bfccc2fe4e9e35cc5459f4f87f3b64f120004482a5fbc5209afb49d09b908379a0e2be7df779ac7f',
  v: '5.131'
})
  .then(function (response) {
    console.log(response);
  });

bridge.send("VKWebAppInit", {});
bridge.send("VKWebAppGetAuthToken", {"app_id": 8057906, "scope": "friends,wall,groups"});
bridge.subscribe((e) => {
  console.log(e);
  if(e.type === 'VKWebAppAccessTokenReceived') {
    console.log(e.data);
    axios.post('https://api.vk.com/method/wall.post', {
      owner_id: -138155699,
      message: 'Hello123',
      from_group: 1,
      access_token: e.data.access_token,
      v: '5.131'
    })
    .then(function (response) {
      console.log(response);
    });
  }});

ReactDOM.render(
  <React.StrictMode>
    <App />
  </React.StrictMode>,
  document.getElementById('root')
);

// If you want to start measuring performance in your app, pass a function
// to log results (for example: reportWebVitals(console.log))
// or send to an analytics endpoint. Learn more: https://bit.ly/CRA-vitals
reportWebVitals();
